import React, { lazy } from 'react';
import { Redirect } from 'react-router-dom';

import AuthLayout from './layouts/Auth';
// import ErrorLayout from './layouts/Error';
import DashboardLayout from './layouts/Dashboard';
// import DashboardAnalyticsView from './views/DashboardAnalytics';
// import DashboardDefaultView from './views/DashboardDefault';
// import OverviewView from './views/Overview';
// import PresentationView from './views/Presentation';
import Login from 'views/Login'
const routes = [
  {
    path: '/',
    exact: true,
    render() {
      return (
        <Login />
      );
    }
    //component: () => <Redirect to="/auth/login" />
  },
  {
    path: '/auth',
    component: AuthLayout,
    routes: [
      {
        path: '/auth/login',
        exact: true,
        component: lazy(() => import('views/Login'))
      },
      {
        component: () => <Redirect to="/errors/error-404" />
      }
    ]
  },
  {
    route: '*',
    component: DashboardLayout,
    routes: [
      {
        path: '/services',
        exact: true,
        component: lazy(() => import('views/Services/Service'))
      },
      {
        path: '/products',
        exact: true,
        component: lazy(() => import('views/Product/Products'))
      },
      {
        path: '/packages',
        exact: true,
        component: lazy(() => import('views/Packs/Packs'))
      },
      {
        path: '/packages-create',
        exact: true,
        component: lazy(() => import('views/Packs/CreatePack'))
      },
      {
        path: '/reports/services',
        exact: true,
        component: lazy(() => import('views/Reports/ReportServices'))
      },
      {
        path: '/customers',
        exact: true,
        component: lazy(() => import('views/Person/Person'))
      },
      {
        path: '/create-zone',
        exact: true,
        component: lazy(() => import('views/Map/ZoneCreate'))
      },
      {
        path: '/view-zone',
        exact: true,
        component: lazy(() => import('views/Map/MapView'))
      },
      {
        path: '/edit-zone/:id',
        exact: true,
        component: lazy(() => import('views/Map/MapEdit'))
      },
      {
        path: '/setting/alert-service',
        exact: true,
        component: lazy(() => import('views/Configuration/AlertService'))
      },
      {
        path: '/setting/authorized-emails',
        exact: true,
        component: lazy(() => import('views/Configuration/AuthorizationEmail'))
      },

    ]
  }
];

export default routes;
