import gql from 'graphql-tag'

export const  Menu_Role = gql`
    query getMenuByRole($role: String) {
        getMenuByRole(role: $role) {
            title
            pages {
                    title
                    href
                    icon
                    children {
                        title
                        href
                        icon,
                            children {
                            title
                            href
                            icon
                        }
                }   
            }
        }
    }
`

export const  Menus = gql`
    query getMenu {
        getMenu {
            id
            title
            pages {
                    id
                    title
                    href
                    icon
                    roles
                    children {
                        id
                        title
                        href
                        icon
                        roles
                }   
            }
        }
    }
`
