import gql from 'graphql-tag'

export const Authenticate_Person = gql`
  mutation authenticatePerson($username: String!, $password: String!) 
  {
    authenticatePerson(username: $username, password: $password) {
      status
      token
      person
      message
      photo
    }
  }
`

export const Create_Person = gql`
  mutation createPerson($input: PersonInput, $file: Upload) 
  {
    createPerson(input: $input, file: $file)
    {
      status
      message
    }
  }
`

export const Update_Person = gql`
  mutation updatePerson($input: PersonInput) 
  {
    updatePerson(input: $input)
    {
      id
      names
    }
  }
`

export const Delete_Person = gql`
  mutation deletePerson($id:ID) {
    deletePerson(id: $id) 
  } 
`

export const Restore_Person = gql`
  mutation restorePerson($id:ID) {
    restorePerson(id: $id) 
  } 
`